<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input v-model="queryInfo.query" placeholder="" clearable
                    @clear="getOrderList">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!-- table  -->
      <el-table :data="orderList" stripe border>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label="" width="80"></el-table-column>
        <el-table-column prop="pay_status" label="" width="80">
          <template slot-scope="scope">
            <!-- 0 1  -->
            <el-tag v-if="scope.row.pay_status == '0'" type="danger"></el-tag>
            <el-tag v-else-if="scope.row.pay_status == '1'" type="success"></el-tag>
            <el-tag v-else type="warning"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="80"></el-table-column>
        <el-table-column prop="create_time" label="" width="180">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="130">
          <template slot-scope="scope">
            <el-button @click="showBox(scope.row.order_id, scope.row.order_price)" type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button @click="showProgressBox" type="success" icon="el-icon-location" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
        title=""
        :visible.sync="updateDialogVisible"
        width="50%"
        @close="updateDialogClosed">

      <!--  -->
      <el-form :rules="updateRules" ref="updateFormRef" :model="updateForm" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="updateForm.address1" expand-trigger="hover"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="updateForm.address2"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="updateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="updateOrder"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
        title=""
        :visible.sync="progressDialogVisible"
        width="50%">
      <el-timeline>
        <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import { getOrderList, getKuaidiById, updateOrder } from 'network/order'
import cityData from './citydata'

export default {
  name: 'Order',
  data () {
    return {
      queryInfo: {
        query: '',
        pagesize: 10,
        pagenum: 1
      },
      orderList: [],
      total: 0,
      // 
      updateDialogVisible: false,
      updateForm: {
        address1: [],
        address2: ''
      },
      updateRules: {
        address1: [{
          required: true,
          message: '/',
          trigger: 'blur'
        }],
        address2: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      cityData,
      // 
      progressDialogVisible: false,
      // 
      progressInfo: [],
      //  id
      order_id: 0,
      //  
      order_price: 0
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    getOrderList () {
      getOrderList(this.queryInfo).then(res => {
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }

        this.$message.success('!')
        this.orderList = res.data.goods
        console.log(this.orderList)
        this.total = res.data.total
      })
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange (newNum) {
      this.queryInfo.pagenum = newNum
      this.getOrderList()
    },
    // 
    showBox (id, price) {
      this.order_id = id
      this.order_price = price
      this.updateDialogVisible = true
    },
    // 
    updateDialogClosed () {
      this.$refs.updateFormRef.resetFields()
      this.order_id = 0
      this.order_price = 0
    },
    // 
    showProgressBox () {
      // 
      getKuaidiById('1106975712662').then(res => {
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }

        this.progressInfo = res.data
        this.progressDialogVisible = true
      })
    },
    // 
    updateOrder () {
      updateOrder(this.order_id, {
        consignee_addr: this.updateForm.address1 + ' ' + this.updateForm.address2,
        order_price: this.order_price
      }).then(res => {
        if (res.meta.status !== 201) {
          this.$message.error('!')
        }

        this.$message.success('!')
        this.updateDialogVisible = false
        this.getOrderList()
      })
    }
  }
}
</script>

<style scoped>
@import '../../plugins/element/timeline/timeline.css';
@import '../../plugins/element/timeline-item/timeline-item.css';

.el-cascader {
  width: 100%;
}
</style>
