import { request } from './request'

// 
export function getOrderList (queryInfo) {
  return request({
    url: '/orders',
    params: queryInfo
  })
}

// 
export function updateOrder (id, data) {
  return request({
    url: `/orders/${id}`,
    method: 'put',
    data
  })
}

// 
export function getKuaidiById (id) {
  return request({
    url: `/kuaidi/${id}`
  })
}
